<template>
  <div class="searchBar" id="searchBar">
    <div class="title col-3">
      <div>
        <span>标&nbsp;&nbsp;&nbsp;&nbsp;题</span>
        <el-input size="small" v-model="title" type="text"></el-input>
      </div>
    </div>
    <div class="creater col-3">
      <div>
        <span>创建者</span>
        <el-input size="small" v-model="author" type="text"></el-input>
      </div>
    </div>
    <div class="time col-6">
      <div>
        <span>更新时间</span>
        <div class="block">
          <el-date-picker
            size="small"
            v-model="startAt"
            placeholder="开始日期"
            :picker-options="pickerOptions0">
          </el-date-picker>
        </div>
        -
        <div class="block">
          <el-date-picker
            size="small"
            v-model="endAt"
            placeholder="结束日期"
            :picker-options="pickerOptions1">
          </el-date-picker>
        </div>
      </div>
    </div>
    <div class="state col-3">
      <div>
        <span>状&nbsp;&nbsp;&nbsp;&nbsp;态</span>
        <el-select size="small" v-model="state">
          <el-option value="">--请选择--</el-option>
          <el-option v-for="(x, y) in status" :value="y" :label="x"></el-option>
        </el-select>
      </div>
    </div>
    <div class="type col-3">
      <div>
        <span>类&emsp;型</span>
        <el-select size="small" v-model="type">
          <el-option value="">--请选择--</el-option>
          <el-option v-for="(x, y) in types" :value="y" :label="x"></el-option>
        </el-select>
      </div>
    </div>
    <div class="function col-6">
      <div>
        <button @click="clear">清空</button>
        <button @click="search">搜索</button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    inject: ['reload'],
    name: 'SearchBar',
    data() {
      return {
        status: window._const.status,
        types: window._const.types,
        title: '',
        author: '',
        startAt: '',
        endAt: '',
        state: '',
        type: '',
        article: {},
        // 时间选择器限制
        pickerOptions0: {
          disabledDate: (time) => {
            if (this.endAt != '') {
              let currentTime = this.endAt;
              let threeMonths = currentTime.setMonth(currentTime.getMonth() - 1);
              currentTime.setMonth(currentTime.getMonth() + 1)
              return time.getTime() > this.endAt || time.getTime() < threeMonths;
            } else {
              return time.getTime() > Date.now();
            }
          }
        },
        pickerOptions1: {
          disabledDate: (time) => {
            if (this.startAt != "") {
              let currentTime = this.startAt;
              let threeMonths = currentTime.setMonth(currentTime.getMonth() + 1);
              currentTime.setMonth(currentTime.getMonth() - 1)
              return time.getTime() < this.startAt || time.getTime() > threeMonths;
            } else {
              return time.getTime() > Date.now();
            }
          }
        },
      }
    },
    computed: {
      page: function () {
        if (this.$route.query.page == undefined) {
          return 1
        } else {
          return this.$route.query.page
        }
      },
      size: function () {
        if (this.$route.query.size == undefined) {
          return 10
        } else {
          return this.$route.query.size
        }
      }
    },
    methods: {
      clear: function () {
        this.title = ''
        this.author = ''
        this.startAt = ''
        this.endAt = ''
        this.type = ''
        this.state = ''
      },
      search() {
        //如果时间为空，获取的时候值为NaN，所以需要将NaN转化为空字符串
        let time = function (time) {
          if (isNaN(new Date(time).getTime()) == true) {
            return ''
          } else {
            return new Date(time).getTime()
          }
        }
        this.article = {
          title: this.title,
          author: this.author,
          startAt: time(this.startAt),
          endAt: time(this.endAt),
          type: this.type,
          status: this.state,
          page: '1',
          size: this.size
        }
        this.$router.push({
          name: 'Article',
          query: this.article
        })
        this.reload()
        //   this.$axios({
        //   method: 'get',
        //   url: 'api/a/article/search',
        //   params: this.article
        // })
        //   .then(() => {
        //     this.$router.push({
        //       name:'Article',
        //       query: this.article
        //     })
        //     this.reload()
        //   })
      },
    },
  }
</script>

<style>
  .searchBar {
    display: flex;
    padding: 10px;
    flex-wrap: wrap;
    height: 109px;
    background-color: #fbfbfb;
    border: 1px solid #cccccc;
  }

  .searchBar select {
    height: 25px;
  }

  .searchBar > .function > div {
    justify-content: flex-end;
  }

  .searchBar > .function > div > button {
    flex: 0;
    flex-basis: 20%;
    margin: 0 5px;
  }

  .searchBar > .state > div > input {
    flex: 0;
    width: 300px;
  }

  .searchBar > .type > div > select {
    width: 0;
    flex-grow: 1;
    flex-shrink: 1;
  }

  .searchBar > .time > div {
    justify-content: flex-end;
  }

  .searchBar > div > div {
    display: flex;
    align-items: center;
    width: 100%;
  }

  .searchBar > div > div > span {
    padding-right: 16px;
    word-break: keep-all;
  }

  .searchBar > div > div > input {
    width: 0;
    flex-grow: 1;
    flex-shrink: 1;
  }

  .searchBar > div > div > select {
    width: 50%;
    text-align-last: center;
  }
</style>
